"use client"

import { motion, useScroll, useTransform } from "framer-motion"
import { useRef } from "react"

export function IntroSequence() {
  const containerRef = useRef<HTMLDivElement>(null)
  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start start", "end start"],
  })

  const opacity = useTransform(scrollYProgress, [0, 0.5], [1, 0])
  const scale = useTransform(scrollYProgress, [0, 0.5], [1, 0.8])
  const y = useTransform(scrollYProgress, [0, 0.5], [0, -100])
  const letterSpacing = useTransform(scrollYProgress, [0, 0.3], [0, 20])

  return (
    <div ref={containerRef} className="relative min-h-[200vh]">
      <motion.div
        style={{ opacity, scale, y }}
        className="sticky top-0 flex min-h-screen flex-col items-center justify-center px-4"
      >
        <motion.div className="text-center">
          <motion.h1
            style={{ letterSpacing }}
            className="bg-gradient-to-b from-white via-neutral-300 to-neutral-600 bg-clip-text text-6xl font-bold tracking-tighter text-transparent sm:text-8xl md:text-9xl"
          >
            AETHERMIND
          </motion.h1>
          <motion.p
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.5, duration: 1 }}
            className="mt-6 text-lg text-neutral-400 sm:text-xl"
          >
            La próxima generación de inteligencia artificial
          </motion.p>
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 1, duration: 1 }}
            className="mt-12 flex flex-col items-center gap-4"
          >
            <div className="h-16 w-px bg-gradient-to-b from-transparent via-white to-transparent" />
            <span className="text-sm text-neutral-500">Scroll para explorar</span>
          </motion.div>
        </motion.div>
      </motion.div>
    </div>
  )
}
